import React, { Component } from 'react';
import { Link } from 'react-router-dom';

import { Row, Col, Modal, ListGroup } from 'react-bootstrap';
import { Button, Chip } from '@material-ui/core';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faBoxOpen, faUserFriends } from '@fortawesome/free-solid-svg-icons'
import ShareIcon from '@material-ui/icons/Share';
import bookmark from './images/border-bookmark.png';
import business from './images/business-placeholder.png'

export default class SearchResultCard extends Component {
    constructor(props) {
        super(props);
        this.state = []
    }
    modelContent = () => {
        return (
            <Row className="m-0">
                <Col md={12} className="p-2">
                    <Row className="align-items-top m-0">
                        <Col md={8} xs={12} className="w-100 text-left">
                            <h5 className="captialize">Job Description</h5>
                            <p className="text-orange d-inline-block pr-1 m-0">{this.state.selectedJob.company ? this.state.selectedJob.company.name : ''}</p>
                            <p className="text-grey d-inline-block pl-2 m-0">{this.state.selectedJob.created_at ? this.state.selectedJob.created_at : ''}</p>
                            <p>{this.state.selectedJob.brif && this.state.selectedJob.brif}</p>
                        </Col>
                    </Row>
                    <Row className="pt-1 m-0">
                        <Col xs={4} className="text-left mb-1 text-center-sm">
                            <FontAwesomeIcon icon={faUserFriends} size="lg" className="text-dark" />
                            <span className="pl-1">{this.state.selectedJob.applications_count + ' Applicant'}</span>
                        </Col>
                        <Col xs={4} className="text-left mb-1 text-center-sm">
                            <FontAwesomeIcon icon={faBoxOpen} size="lg" className="text-dark" />
                            <span className="pl-1">{this.state.selectedJob.questions && this.state.selectedJob.questions.length + ' Question'}</span>
                        </Col>
                        <Col xs={12} className="text-left mb-1 mt-2">
                            <p className="p-0 mb-1 bold">{ this.state.selectedJob.tags && 'Tags'}</p>
                            {this.state.selectedJob.tags?.map(tag =>
                                <Chip className="m-1" key={tag.name} label={tag.name} />
                            )}
                        </Col>
                        <Col xs={12} className="text-left">
                            {this.state.selectedJob.questions.length > 0 && <p className="p-0 mb-2 bold">Questions</p>}
                            <ListGroup>
                                {this.state.selectedJob.questions?.map((question, index) =>
                                    <ListGroup.Item style={{ width: 'fit-content' }} key={question.title} className="mb-2 border p-1 bg-lightgrey">{index + 1 + '- ' + question.title}</ListGroup.Item>
                                )}
                            </ListGroup>
                        </Col>
                    </Row>
                </Col>
            </Row>
        )
    }
    componentWillMount() {
    }
    handleShow = (data) =>{
        this.setState({...this.state ,  selectedJob : data , show : true })
    }
    handleClose = () => {
        this.setState({...this.state , show : false })
    }
    render() {
        let jobs = this.props.data
        return (
            <div className="pt-4 pb-4 border-bottom w-100">
                {jobs?.map(job =>
                    <Row className="justify-content-center align-items-center m-0">
                        <Col md={1} xs={6} className="order-sm-1 order-2 text-center pr-1 pl-1 ">
                             <button 
                                    className="bg-transparent border-0 " 
                                    onClick={e => this.handleShow(job)}>
                                 <img 
                                    src={job.company.logo?job.company.logo.url : business} 
                                    alt="company logo" 
                                    className="rounded w-100 text-left mb-3" />
                            </button>
                        </Col>
                        <Col md={11} xs={12} className="order-sm-2 order-3 text-left">
                            <Row className="m-0">
                                <Col md='8' className="order-sm-1 order-2 p-0  d-inline-block">
                                    <button 
                                        className="bg-transparent border-0 text-left" 
                                        onClick={e => this.handleShow(job)}>
                                        <h5>{job.title}</h5>
                                        <p className="text-orange d-inline-block pr-1">{job.company.name}</p>
                                        <p className="text-grey d-inline-block">{job.created_at}</p>
                                        <p>{job.brif && job.brif.length > 191 ? 
                                            <div>{job.brif.substring(0,191)}
                                                <button  
                                                className="bg-transparent border-0 text-orange underline"
                                                onClick={e => this.handleShow(job)}
                                                > See More</button></div> 
                                            : job.brif }
                                        </p>
                                    </button>
                                </Col>
                                <Col md='4' className="text-right order-sm-2 p-0 order-1 mb-1 d-inline-block text-responsive-right">
                                    {/* <Button size="large" variant="outlined" className="btn-orange-outline-gradiant" color="primary">
                                        <ShareIcon /> share
                                    </Button> */}
                                    <Link className="pl-3">
                                        <img src={bookmark} className="btn-bookmark" width="20" alt="bookmark" />
                                    </Link>
                                </Col>
                            </Row>
                        </Col>
                    </Row>
                )}
                {this.state.selectedJob &&
                    <Modal
                        size="lg"
                        className="row m-0  pr-0"
                        show={this.state.show} onHide={this.handleClose}>
                        <Col xs={12} className="p-0">
                            <Modal.Header className=" p-0 bg-lightgrey row m-0">
                                <Modal.Title className="h5 col-8 p-2 pr-3 pl-3 captialize" >{this.state.selectedJob.title}</Modal.Title>
                            </Modal.Header>
                            <Modal.Body className="p-0">
                                <div key={this.state.selectedJob.id}>
                                    {this.modelContent()}
                                </div>
                            </Modal.Body>
                        </Col>
                    </Modal>
                }
            </div>
        );
    }
}